import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { AuthProvider } from './contexts/AuthContext';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import DashboardLayout from './components/layout/DashboardLayout';
import DashboardOverview from './components/dashboard/DashboardOverview';

// Lazy load other routes for better performance
const MealPlanner = React.lazy(() => import('./pages/MealPlanner'));
const Supplements = React.lazy(() => import('./pages/Supplements'));
const ShoppingList = React.lazy(() => import('./pages/ShoppingList'));
const Progress = React.lazy(() => import('./pages/Progress'));
const DailyLog = React.lazy(() => import('./pages/DailyLog'));
const Learn = React.lazy(() => import('./pages/Learn'));
const Settings = React.lazy(() => import('./pages/Settings'));
const Profile = React.lazy(() => import('./pages/UserProfile'));

// Layouts
import MainLayout from './components/layouts/MainLayout';

// Pages
import Home from './pages/Home';
import RecipeSearch from './pages/RecipeSearch';
import RecipeDetail from './pages/RecipeDetail';
import Dashboard from './pages/Dashboard';
import SavedRecipes from './pages/SavedRecipes';
import UserProfile from './pages/UserProfile';
import Login from './pages/Login';
import Contact from './pages/Contact';

// Auth
import ProtectedRoute from './components/auth/ProtectedRoute';

// Create a client
const queryClient = new QueryClient();

const App: React.FC = () => {
  return (
    <QueryClientProvider client={queryClient}>
      <AuthProvider>
        <Router>
          <React.Suspense
            fallback={
              <div className="flex items-center justify-center min-h-screen">
                <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-blue-500" />
              </div>
            }
          >
            <Routes>
              {/* Public routes */}
              <Route path="/login" element={<Login />} />
              <Route path="/signup" element={<div>Signup Page</div>} />
              
              {/* Protected routes */}
              <Route path="/" element={<MainLayout />}>
                <Route index element={<Home />} />
                <Route path="search" element={<RecipeSearch />} />
                <Route path="recipe/:id" element={<RecipeDetail />} />
                <Route path="login" element={<Login />} />
                <Route path="contact" element={<Contact />} />
                
                {/* Protected Routes */}
                <Route
                  path="dashboard"
                  element={
                    <ProtectedRoute>
                      <DashboardLayout />
                    </ProtectedRoute>
                  }
                >
                  <Route index element={<DashboardOverview />} />
                  <Route path="meals" element={<MealPlanner />} />
                  <Route path="supplements" element={<Supplements />} />
                  <Route path="shopping" element={<ShoppingList />} />
                  <Route path="progress" element={<Progress />} />
                  <Route path="log" element={<DailyLog />} />
                  <Route path="learn" element={<Learn />} />
                  <Route path="settings" element={<Settings />} />
                  <Route path="profile" element={<Profile />} />
                </Route>
                <Route
                  path="saved"
                  element={
                    <ProtectedRoute>
                      <SavedRecipes />
                    </ProtectedRoute>
                  }
                />
              </Route>

              {/* Catch all route */}
              <Route path="*" element={<Navigate to="/dashboard" replace />} />
            </Routes>
          </React.Suspense>
        </Router>
        <ToastContainer position="bottom-right" />
      </AuthProvider>
    </QueryClientProvider>
  );
};

export default App;
